.test-window{
  &__indicator{
    display: flex;
    margin-bottom: 3.875rem;
    height: 0.1875rem;
    width: 100%;
    @include respondTo(small) {
      margin-bottom: 2.625rem;
    }
  }
  &__indicator-item{
    flex-grow: 1;
    background-color: #6B7081;
    &.active{
      background-color: #FEC403;
    }
  }
}
