
.tax-deduction{
  //*************************
  &__title-section{
    position: relative;
    padding-top: 148px;
    padding-bottom: 293px;
    @media screen and (max-width: 1300px){
      padding-top: 11vw;
      padding-bottom: 26vw;
    }
    @media screen and (max-width: 990px){
      padding-top: 5vw;
      padding-bottom: 18vw;
    }
    @media screen and (max-width: 480px){
      padding-top: 35px;
      padding-bottom: 95px;
    }
  }
  &__wrap-title{
    position: relative;
    z-index: 7;
    width: 100%;
    max-width: 855px;
    @media screen and (max-width: 1300px){
      width: 60vw;
    }
    @media screen and (max-width: 560px){
      width: 100%;
    }
  }
  &__title{
    font-size: 48px;
    line-height: 48px;
    letter-spacing: -0.015em;
    color: #1C1B28;
    font-family: $ff-bold;
    @media screen and (max-width: 770px){
      font-size: 32px;
      line-height: 32px;
      letter-spacing: -0.015em;
    }
    @media screen and (max-width: 480px){
      font-size: 26px;
      line-height: 26px;
    }
  }
  &__sub-title{
    margin-top: 40px;
    font-size: 20px;
    line-height: 28px;
    width: 100%;
    max-width: 730px;
    @media screen and (max-width: 770px){
      font-size: 16px;
      line-height: 20px;
    }
    @media screen and (max-width: 770px){
      margin-top: 30px;
    }
  }
  &__wrap-img{
    overflow: hidden;
    position: absolute;
    top: 23px;
    right: 0;
    width: 100%;
    max-width: 604px;
    background: linear-gradient(51.11deg, rgba(229, 229, 229, 0) 3.41%, rgba(255, 172, 100, 0.1) 29.89%, #FFB16D 102.04%, #FFB16D 102.04%);
    border-radius: 50%;
    &:before{
      content: "";
      display: block;
      padding-top: 100%;
    }
    @media screen and (max-width: 1300px){
      width: 47vw;
    }
    @media screen and (max-width: 990px){
      width: 37vw;
    }
    @media screen and (max-width: 560px){
      display: none;
    }
    img{
      position: absolute;
      right: 0;
      bottom: 0;
    }
  }
  //  **********************
  &__section-useful-information{
    padding-top: 63px;
    padding-bottom: 52px;
  }

  &__row{
    display: grid;
    grid-template-columns: minmax(300px, 812px) minmax(300px, 396px);
    grid-column-gap: 20px;
    grid-row-gap: 60px;
    @media screen and (max-width: 1130px){
      grid-template-columns:1fr;
    }
    @media screen and (max-width: 480px){
      grid-row-gap: 23px;
    }
    .mortgage-calculator-link.green .mortgage-calculator-link__title{
      @media screen and (max-width: 760px){
        font-size: 20px;
        line-height: 28px;
      }
      @media screen and (max-width: 480px){
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
  &__left-col{

  }
  &__questions{

  }
  &__questions-item{
    &:not(:first-child){
      margin-top: 92px;
      @media screen and (max-width: 480px){
        margin-top: 64px;
      }
    }
  }
  &__questions-title{
    display: flex;
    align-items: flex-start;
    @media screen and (max-width: 760px){
      font-size: 26px;
      line-height: 26px;
    }
    @media screen and (max-width: 480px){
      flex-direction: column;
    }
    h2{
      padding-top: 16px;
      margin-left: 20px;
      font-size: 40px;
      line-height: 44px;
      color: #1C1B28;
      letter-spacing: -0.015em;
      font-family: $ff-bold;
      @media screen and (max-width: 760px){
        font-size: 32px;
        line-height: 32px;
      }
      @media screen and (max-width: 510px){
        font-size: 26px;
        line-height: 26px;
      }
      @media screen and (max-width: 510px){
        margin-left: 0;
      }
    }
  }
  &__questions-circle{
    flex-shrink: 0;
    position: relative;
    p{
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      left: 28px;
      font-size: 40px;
      line-height: 44px;
      color: #fff;
      &.double-digit{
        left: 13px;
        @media screen and (max-width: 630px){
          left: 10px
        }
      }
      @media screen and (max-width: 630px){
        font-size: 26px;
        line-height: 26px;
        left: 20px
      }
    }
  }
  &__questions-circle-img{
    width: 84px;
    @media screen and (max-width: 630px){
      width: 58px;
    }
  }
  &__footnote{
    display: flex;
    margin-top: 68px;
    background: #1C1B28;
    border-radius: 40px;
    padding: 48px 32px;
    position: relative;
    z-index: 10;

    @media screen and (max-width: 480px){
      padding: 30px 16px;
      margin-top: 48px;
    }
    &:after{
      content: "";
      width: 0;
      height: 0;
      display: block;
      position: absolute;
      z-index: 10;
      border: 0;
      border-left: 0 solid transparent;
      border-right: 37px solid transparent;
      left: 25.6%;
      border-bottom: 32px solid #1c1b28;
      top: -31px;
      border-top-left-radius: 5px;
      border-top-right-radius: 15px;
    }
    svg{
      flex-shrink: 0;
      margin-right: 32px;
      @media screen and (max-width: 480px){
        display: none;
      }
    }
  }
  &__footnote-des{
    width: 100%;
    max-width: 604px;

    p{
      font-size: 24px;
      line-height: 26px;
      color: #FFFFFF;
      &:not(:first-child){
        margin-top: 26px;
        @media screen and (max-width: 480px){
          margin-top: 17px;
        }
      }
      @media screen and (max-width: 760px){
        font-size: 20px;
        line-height: 24px;
      }
      @media screen and (max-width: 480px){
        font-size: 14px;
        line-height: 18px;
      }
    }
  }
  &__types-deductions{
    margin-top: 44px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 20px;
    @media screen and (max-width: 660px){
      grid-template-columns:1fr;
    }

  }
  &__types-deductions-item{
    background: #FFFFFF;
    border: 1px solid #1C1B28;
    border-radius: 12px;
    padding: 20px 19px 30px 20px;

  }
  &__types-deductions-des{
    p{
      &:first-child{
        margin-top: 0px;
      }
      margin-top: 16px;
      font-size: 18px;
      line-height: 26px;
      color: #1C1B28;
      @media screen and (max-width: 760px){
        font-size: 16px;
        line-height: 20px;
      }
      @media screen and (max-width: 480px){
        font-size: 12px;
        line-height: 16px;
      }
    }
  }
  &__types-deductions-title{
    height: 90px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
    @media screen and (max-width: 660px){
      height: auto;
    }
    h2{
      font-size: 28px;
      line-height: 30px;
      letter-spacing: -0.01em;
      color: #1C1B28;
      font-family: $ff-bold;
      width: 100%;
      max-width: 250px;
      @media screen and (max-width: 760px){
        font-size: 26px;
        line-height: 26px;
      }
      @media screen and (max-width: 660px){
        max-width: 390px;
      }
      @media screen and (max-width: 480px){
        font-size: 20px;
        line-height: 22px;
      }
    }
  }
  &__types-deductions-circle{
    margin-top: 5px;
    width: 64px;
    height: 64px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    &.green{
      background: linear-gradient(180deg, #99F28A 0%, #BDF0FB 100%);
      backdrop-filter: blur(15.21px);
    }
    &.yellow{
      background: linear-gradient(180deg, #FED141 0%, #FFB49C 100%);
      backdrop-filter: blur(15.21px);
    }
    &.blur{
      background: linear-gradient(180deg, #BBE2FF 0%, #65C6DB 100%);
      backdrop-filter: blur(15.21px);
    }
    &.brown{
      background: linear-gradient(180deg, #EEE69B 0%, #D59876 100%);
      backdrop-filter: blur(15.21px);
    }
    &.violet{
      background: linear-gradient(360deg, #96AAF0 4.69%, #EDC7FB 100%);
      backdrop-filter: blur(15.21px);
    }
    @media screen and (max-width: 660px){
      margin-top: 0;
    }
    @media screen and (max-width: 760px){
      width: 48px;
      height: 48px;
    }
  }
  &__types-deductions-img{
    width: 100%;
    max-width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    img{
      max-width: 95%;
    }
  }
  &__deduction-steps{
    margin-top: 61px;
    @media screen and (max-width: 480px){
      margin-top: 27px;
    }
  }
  &__deduction-steps-item{
    display: flex;
    position: relative;
    padding-bottom: 39px;
    @media screen and (max-width: 560px){
      padding-bottom: 0;
    }

    &:not(:last-child){
      &:before{
        content: "";
        display: block;
        position: absolute;
        top:62px;
        left: 32px;
        height: calc(100% - 58px);
        width: 1px;
        background-color: #8BC540;
        @media screen and (max-width: 560px){
          display: none;
        }
      }
    }

  }

  &__deduction-steps-circle{
    flex-shrink: 0;
    margin-right: 40px;
    background-color: #fff;
    position: relative;
    width: 64px;
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #8BC540;
    border-radius: 50%;
    @media screen and (max-width: 560px){
      display: none;
    }

    p{
      color: #1C1B28;
      font-size: 28px;
      line-height: 32px;
      font-family: $ff-bold;
    }

  }
  &__deduction-steps-des{
    padding-top: 21px;

  }
  &__deduction-steps-title{
    margin-bottom: 20px;
    font-size: 22px;
    line-height: 26px;
    font-family: $ff-bold;
    color: #1C1B28;
    @media screen and (max-width: 760px){
      font-size: 20px;
      line-height: 24px;
    }
    @media screen and (max-width: 560px){
      margin-bottom: 0;
    }
    @media screen and (max-width: 480px){
      font-size: 16px;
      line-height: 20px;
    }
  }
  &__stylized-list{
    list-style-position: outside;
    padding-left: 13px;
    list-style-image: url("../img/point.png");
  }
  &__deduction-steps-list{

    li{
      padding-left: 0;
      font-size: 18px;
      line-height: 26px;
      color: #1C1B28;
      a{
        color: #8bc540;
        &:hover{
          color: #72a137;
        }
      }

      @media screen and (max-width: 760px){
        font-size: 16px;
        line-height: 20px;
      }
      @media screen and (max-width: 480px){
        font-size: 12px;
        line-height: 14px;
      }
      //&:before{
      //  content: "";
      //  display: block;
      //  width: 4px;
      //  height: 4px;
      //  border-radius: 50%;
      //  background-color: #67a511;
      //  flex-shrink: 0;
      //  margin-right: 8px;
      //  margin-top: 13px;
      //}
      &:not(:first-child){
        margin-top: 12px;
      }
    }
  }
  &__deduction-steps-more{
    cursor: pointer;
    display: block;
    margin-top: 20px;
    text-transform: uppercase;
    font-size: 12px;
    line-height: 16px;
    font-family: $ff-bold;
    color: #1C1B28;
    letter-spacing: 1.5px;
  }
  &__deduction-steps-explanation{
    margin-top: 20px;
    font-size: 18px;
    line-height: 26px;
    @media screen and (max-width: 560px){
      margin-top: 8px;
    }
  }
  &__section-faq{
    padding-top: 64px;
    padding-bottom: 64px;
  }
  &__faq-title{
    font-size: 40px;
    line-height: 44px;
    font-family: $ff-bold;
    letter-spacing: -0.015em;
    @media screen and (max-width: 760px){
      font-size: 32px;
      line-height: 32px;
    }
    @media screen and (max-width: 480px){
      font-size: 26px;
      line-height: 26px;
    }
  }
  &__faq-row{
    margin-top: 24px;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-column-gap: 20px;
    @media screen and (max-width: 760px){
      grid-template-columns: 1fr;
    }

  }
  &__faq-list{

  }
  &__faq-list-item{
    border-top: 1px solid #CACCDF;
    &:last-child{
      border-bottom: 1px solid #CACCDF;
    }
  }
  &__faq-list-questions{
    cursor: pointer;
    padding: 20px 0;
    font-size: 22px;
    line-height: 26px;
    font-family: $ff-bold;
    @media screen and (max-width: 760px){
      font-size: 20px;
      line-height: 24px;
    }
    @media screen and (max-width: 480px){
      font-size: 16px;
      line-height: 20px;
    }
  }
  &__faq-list-answer{
    max-height: 0;
    overflow: hidden;
    -webkit-transition-duration: .5s;
    -o-transition-duration: .5s;
    transition-duration: .5s;
    p{
      font-size: 16px;
      line-height: 20px;
      &:last-child{
        margin-bottom: 20px;
        margin-top: 3px;
      }
      @media screen and (max-width: 760px){
        font-size: 16px;
        line-height: 20px;
      }
      @media screen and (max-width: 480px){
        font-size: 12px;
        line-height: 14px;
      }
    }
    padding-left: 19px;
    @media screen and (max-width: 760px){
      padding-left: 0;
    }
    @media screen and (max-width: 480px){
      padding-left: 0;
    }
  }
  &__faq-list-answer-des{
    font-size: 16px;
    line-height: 20px;
    &:last-child{
      margin-bottom: 20px;
      margin-top: 3px;
    }
    @media screen and (max-width: 760px){
      font-size: 16px;
      line-height: 20px;
    }
    @media screen and (max-width: 480px){
      font-size: 12px;
      line-height: 14px;
    }
  }
  &__download-documents{
    display: flex;
    align-items: center;
    background: #F1F2F4;
    border-radius: 8px;
    padding: 28px 17px 28px 20px;
  }
  &__download-title{
    font-size: 22px;
    line-height: 26px;
    font-family: $ff-bold;
    @media screen and (max-width: 760px){
      font-size: 20px;
      line-height: 24px;
    }

  }
  &__download-des{
    margin-top: 8px;
    color: #686D82;
    font-size: 14px;
    line-height: 16px;
    @media screen and (max-width: 760px){
      font-size: 12px;
      line-height: 16px;
    }
    @media screen and (max-width: 480px){
      font-size: 12px;
      line-height: 14px;
    }
  }
  &__contacts{
    margin-top: 40px;
    padding-left: 25px;
    @media screen and (max-width: 760px){
      display: none;
    }

  }
  &__contacts-title{
    font-size: 14px;
    line-height: 18px;
    color: #1C1B28;
  }
  &__contacts-value{
    margin-top: 20px;
    a{
      display: block;
      font-size: 32px;
      line-height: 32px;
      font-family: $ff-bold;
      color: #1C1B28;
      &:not(:first-child){
        margin-top: 10px;
      }
    }
  }
  //  
  &__calculator{
    margin-top: 38px;
  }
  &__calculator-row{
    &:not(:first-child){
      margin-top: 27px;
    }
    .range-input__value{
      font-family: $ff-base;
    }
    .range-input__wrapper {
      background-color: #fff;
      border: none;
      input{
        //width: 69px;
      }
      @media screen and (max-width: 979px) {
        height: 3.75rem;
        padding: 0 1.25rem;
        border-radius: 8px;
      }
      @media screen and (max-width: 767px){
        height: 3.75rem;
        padding: 0 1.25rem;
        border-radius: 8px;
      }
      @media screen and (max-width: 480px){
        height: 40px;
      }
    }
    .range-input__slider.noUi-target{
      background-color: transparent;
    }
  }
  &__calculator-label{
    font-size: 18px;
    line-height: 26px;
    margin-bottom: 12px;
    @media screen and (max-width: 800px){
      font-size: 15px;
      line-height: 20px;
      margin-bottom: 8px;
    }
  }
  &__calculator-input{
    width: 100%;
    height: 60px;
    border-radius: 8px;
    //border: 1px solid #CACCDF;
    background-color: #fff;
    overflow: hidden;
    @media screen and (max-width: 480px){
      height: 40px;
    }
    input{
      font-family: $ff-base;
      border: none;
      outline: none;
      color: #1C1B28;
      font-size: 18px;
      line-height: 26px;
      width: 100%;
      height: 100%;
      padding-left: 20px;
      padding-right: 20px;
      &:-moz-placeholder { color: #b7bfd2; }
      &::-webkit-input-placeholder { color: #b7bfd2;}
      @media screen and (max-width: 767px){
        font-size: 1rem;
      }
    }
  }
  &__row-calculations{
    display: flex;
    justify-content: space-between;
    @media screen and (max-width: 510px){
      flex-direction: column;
      align-items: center;
      margin-top: 20px;
    }
  }
  &__sum-deduction{

  }
  &__label{
    text-transform: uppercase;
    font-size: 14px;
    line-height: 16px;
    font-family: $ff-bold;
    letter-spacing: 1.5px;
    width: max-content;
    @media screen and (max-width: 760px){
      font-size: 12px;
      line-height: 16px;
    }
    @media screen and (max-width: 510px){
      margin-top: 27px;
    }
    @media screen and (max-width: 480px){
      font-size: 10px;
      line-height: 12px;
    }
  }
  &__detailed-report{
    cursor: pointer;
    &:after{
      content: "";
      display: block;
      width: 100%;
      height: 2px;
      background-color: #CACCDF;
      margin-top: 2px;
    }
  }
  &__deduction-value{
    margin-top: 9px;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: -0.01em;
    color: #8BC540;
    font-family: $ff-medium;
    @media screen and (max-width: 510px){
      text-align: center;
      margin-top: 15px;
    }
    @media screen and (max-width: 480px){
      font-size: 26px;
      line-height: 26px;
    }
    span{
      font-family: $ff-bold;
    }
  }
  &__cost-flat{
    margin-top: 12px;
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
  &__cost-flat{
    p{
      font-size: 14px;
      line-height: 18px;
    }
  }
  &__period-selection{

  }
  &__select-choosen{
    cursor: pointer;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #fff;
    height: 60px;
    //border: 1px solid #caccdf;
    border-radius: 8px;
    width: 100%;
    @media screen and (max-width: 480px){
      height: 40px;
    }
  }
  &__selected-value{
    font-size: 20px;
    line-height: 28px;
    @media screen and (max-width: 767px){
      font-size: 1rem;
    }
  }
  &__selected-arrow{
    transition-duration: .5s;
    will-change: transform;
  }

  &__wrapper-select{
    position: relative;
    width: 100%;
    &.active{
      .tax-deduction__selected-arrow{
        transform: rotate(180deg);
      }
    }
  }
  &__list-wrapper{
    overflow: hidden;
    max-height: 0;
    transition-duration: .5s;
    will-change: transform;
    position: absolute;
    z-index: 10;
    top: 63px;
    left: 0;
    width: 100%;

    .select__list{
      overflow: hidden;
      border-radius: 8px;
      li{
        &.unactive{
          pointer-events: none;
        }
      }
    }
  }
  &__period-input-selected{
    margin-top: 32px;
    display: flex;
    margin-left: -10px;
    margin-right: -10px ;
    @media screen and (max-width: 720px){
      display: grid;
      grid-template-columns: repeat(2,1fr);
      grid-row-gap: 24px;
    }
    @media screen and (max-width: 530px){
      grid-template-columns: 1fr;
    }
  }
  &__period-input-selected-item{
    width: calc((100% / 3) - 10px);
    margin: 0 10px;
    @media screen and (max-width: 720px){
      width: auto;
    }
    .tax-deduction__calculator-input{
      padding-left: 20px;
      padding-right: 20px;
      display: flex;
      align-items: center;
      p{
        font-size: 18px;
        line-height: 17px;
        @media screen and (max-width: 767px){
          font-size: 1rem;
        }
      }
    }
    input{
      width: 56px;
      padding: 0;
      //max-width: 120px;
      @media screen and (max-width: 767px){
        width: 50px;
      }
    }
    .tax-deduction__period-input-wrapper{
      width: max-content;
    }
  }
  &__numeric-set{
    padding-left: 20px;
    padding-right: 20px;
    display: flex;
    align-items: center;
    p {
      font-size: 18px;
