      }
    }
  }
  &__checkbox label {
    position: relative;
    display: inline-block;
    padding-left: 24px;
    font-size: 18px;
    line-height: 26px;
    color: #1C1B28;
    @media screen and (max-width: 590px){
      font-size: 15px;
      line-height: 23px;
    }
  }

  &__checkbox label::before {
    height: 16px;
    width: 16px;
    border: 2px solid #686D82;
    box-sizing: border-box;
    left: 0;
    top: 5px;
    @media screen and (max-width: 590px){
      height: 14px;
      width: 14px;
      top: 4px;
    }
  }

  &__checkbox label::after {
    height: 6px;
    width: 10px;
    border-left: 2px solid #246900;
    border-bottom: 2px solid #246900;
    transform: rotate(-59deg);
    left: 3px;
    top: 9px;
    @media screen and (max-width: 590px){
      height: 5px;
      width: 9px;
      left: 3px;
      top: 8px;
    }
  }

