.map-filter{
  display: flex;
  width: 100%;
  max-width: 1164px;
  margin: 0 auto;
  position: absolute;
  top: 2rem;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  width: calc(100% - 4rem);

  @media screen and (max-width: 1440px) {
    width: calc(100% - 4rem);
    max-width: unset;
  }

  @media screen and (max-width: 1280px) {
    width: calc(100% - 3rem);
  }

  @include respondTo(tablet){
    top: 1.5rem;
    left: 1.5rem;
    width: auto;
    transform: none;
  }

  @include respondTo(mobile){
    left: 0;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    // top: 1.25rem; //20px ..    
    top: 3rem; //48px
    overflow-x: auto;
    padding-bottom: 1rem;
    width: 100vw;
  }

  &__item{
    @extend %btn-reset;

    font-family: $ff-bold;
    font-size: .75rem; //12px
    line-height: 1.3;
    white-space: nowrap;
    border-radius: 8px;
    height: 2rem; //32px
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: .5rem 1rem;
    color: $color-text;
    background-color: #fff;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    transition: background-color ease .33s;

    @include respondTo(mobile){
      font-size: .625rem; //10px
      line-height: 1.2;
      padding: .5rem .75rem; //8px 12px
      height: 1.75rem; //28px
    }

    &:not(:last-child){
      margin-right: .75rem; //12px

      @include respondTo(mobile){
        margin-right: .5rem; //8px
      }
    }

    &.is-active{
      pointer-events: none;
      background-color: $color-card-yellow;
    }
  }
}
.ymaps-2-1-79-balloon{
  background-color: green;
}
.map-label{
  position: relative;
  top: -71px;
  left: -21px;
  display: flex;
  align-items: center;
  &__img{
    width: 46px;
    margin-right: 5px;
    flex-shrink: 0;
  }
  &__die{
    background-color: #fff;
    padding: 5px;
    border-radius: 5px;
    margin-top: -7px;
    p{
      font-family: $ff-bold;
      font-size: 14px;
      width: 155px;
    }
  }
}
