ul.bulleted-list{
  margin-top: 2rem;
  margin-bottom: 1.25rem;
  li{
    position: relative;
    display: flex;
    margin-top: 0.312rem;
    &:not(:last-child){
      margin-bottom: 1.25rem;
    }
    &:before{
      content: "";
      position: relative;
      left: 0;
      top: 0;
      margin-top: 0.25rem;
      display: block;
      width: 1.0625rem;
      height: 1.0625rem;
      border-radius: 50%;
      margin-right: 0.44rem;
      flex-shrink: 0;
    }
  }
  &.green{
    li{
      &:before{
        background-color: #8bc540;
      }
      &:after{
        content: "";
        display: block;
        position: absolute;
        height: 0.25rem;
        width: 0.5rem;
        border-left:0.0625rem solid #fff;
        border-bottom:0.0625rem solid #fff;
        transform: rotate(-62deg);
        left: 0.25rem;
        top: 0.625rem;
      }
    }
  }
  &.yellow{
    li{
      &:before{
        background-color: #ffbc4b;
      }
      &:after{
        position: absolute;
        left: 6px;
        top: 6px;
        content:"x";
        display: block;
        color: #fff;
        font-size: 11px;
        line-height: normal;
      }
    }
  }
}
