
.block-stories{
  min-height: 160px;
  position: relative;
  z-index: 21;
  &:before{
    content: '';
    box-sizing: border-box;
    position: absolute;
    z-index: 53;
    top: 50%;
    left: 50%;
    width: 20px;
    height: 20px;
    margin-top: -10px;
    margin-left: -10px;
    border-radius: 50%;
    border: 2px solid #ccc;
    border-top-color: #333;
    animation: spinner .6s linear infinite;
    text-indent: 100%;
    white-space: nowrap;
    overflow: hidden;
  }
  &:after{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 51;
    background-color: #ffffff;
  }
  &.active{
    &:before{
      display: none;
    }
    &:after{
      display: none;
    }

  }
  //@media screen and (max-width:767px){
  //  width: calc(100% + 4.062rem);
  //}
  //@media screen and (max-width:480px){
  //  width: calc(100% + 1.25rem);
  //}

}

@keyframes spinner {
  to {
    transform: rotate(360deg);
  }
}
