    height: 46px;
    background-color: #fff;
    border-radius: 50%;
    border: 2px solid #CACCDF;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1003;
    @media screen and (max-width: 520px){
      background-color: rgba(0, 0, 0, 0.49);
      top: 78px;
      right: 10px;
      border: none;
      width: 32px;
      height: 32px
    }
    svg{
      path{
        @media screen and (max-width: 520px){
