.carousel {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  &__list {
    display: flex;
    //overflow-x: hidden;
    list-style: none;
    position: relative;
    width: 100%;
    height: 100%;
    justify-content: center;
    perspective: 300px;
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: hidden;
    background-color: #d9d8d8;
    width: 100%;
    max-width: 450px;
    height: 100%;
    border-radius: 12px;
    position: absolute;
    transition: all .5s linear;
    will-change: transform;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
    &.shadow{
       box-shadow: 0 0 34px 2px rgba(0, 0, 0, 0.73);
    }
    &.opacity{
      opacity: 0;
    }
    &.active{
      .carousel__inner-slider-item-plug{
        display: none;
      }
      .carousel__inner-slider-paggination{
        //display: none;
        display: flex;
      }
      .carousel__inner-slider-arrow{
        width: 30%;
      }
    }

  }
  &__navigation{
    width: 100%;
    position: absolute;
  }
  &__navigation--item{
    cursor: pointer;
    position: absolute;
    top: 0;
    width: 32px;
    height: 32px;
    border-radius: 50%;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 200;
    &.unactive{
      display: none;
    }

  }
  &__navigation--next{
    left: -60px;
  }
  &__navigation--prev{
    right: -60px;
  }
  &__navigation--again{
    right: -60px;
  }
  &__inner-slider-container{
    width: 100%;
    height: 100%;
    position: relative;

  }
  &__inner-slider-item{
    position: relative;
    width: 100%;
    height: 100%;
    flex-direction: column;
    grid-template-columns: 1fr;
    //grid-auto-rows: auto;
    //align-content: end;
    padding: 40px 32px 50px;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: #8bc540 #caccdf;
    scrollbar-width: thin;
    &::-webkit-scrollbar {
      width: 2px;
      height: 2px;
    }
    &::-webkit-scrollbar-button {
      width: 0;
      height: 0;
    }
    &::-webkit-scrollbar-thumb {
      background: #8bc540;
      border: 0 #fff;
      border-radius: 50px;
    }
    &::-webkit-scrollbar-track {
      background: #caccdf;
      border: 0 #fff;
      border-radius: 50px;
    }
    &::-webkit-scrollbar-corner {
      background: transparent;
    }
    &.active{
      display: flex;
    }
    &.unactive{
      display: none;
    }
    &[for-id-location="location-up"]{

    }
    &[for-id-location="location-down"]{
      grid-auto-rows: auto;
      align-content: end;
    }
    @media screen and (max-width: 520px){
      padding: 40px 28px 30px;
    }

  }
  &__inner-slider-main-img{
    z-index: 1;
    &.ground{
      position: fixed;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      overflow: hidden;
      img{
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &.up{
      position: relative;
      order: -1;
      width: 100%;
      max-width: 385px;
      margin: 0 auto 0;
      display: flex;
      justify-content: center;
      margin-top: 10px;
      img{
        max-width: 100%;
        height: auto;
      }
    }
    &.down{
      position: relative;
      order: 5;
      width: 100%;
      max-width: 385px;
      margin: 30px auto 10px;
      display: flex;
      justify-content: center;
      img{
        max-width: 100%;
        height: auto;
      }
    }
  }
  &__inner-slider-container-des{
    position: relative;
    z-index: 10;
    overflow-y: auto;
    overflow-x: hidden;
    scrollbar-color: #8bc540 #caccdf;
    scrollbar-width: thin;
    &::-webkit-scrollbar {
      width: 2px;
      height: 2px;
    }

    &::-webkit-scrollbar-button {
      width: 0;
      height: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: #8bc540;
      border: 0 #fff;
      border-radius: 50px;
    }

    &::-webkit-scrollbar-track {
      background: #caccdf;
      border: 0 #fff;
      border-radius: 50px;
    }

    &::-webkit-scrollbar-corner {
      background: transparent;
    }
    &[for-text-layout="up"]{
      margin-top: 40px;
      order: 1;
      height: 100%;
      display: flex;
      flex-direction: column;
    }
    &[for-text-layout="down"]{
      margin-top: auto;
      order: 3;
    }
  }
  &__inner-slider-wr-des{
    //margin-bottom: 32px;
    position: relative;
    z-index: 10;
    &[for-text-layout="up"]{
      order: 1;
      margin-top: 10px;
      //display: flex;
      //flex-direction: column;
    }
    &[for-text-layout="down"]{
      margin-top: 30px;
      margin-bottom: 30px;
      order: 3;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
    }
    &.color-dark{
      p{
        color: #072833;
      }
      .carousel__inner-slider-title{
        color: #072833;
      }
    }
    &.color-light{
      p{
        color: #FFFFFF;
      }
      .carousel__inner-slider-title{
        color: #FFFFFF;
      }
    }
  }
  &__inner-slider-title{
    font-family: $ff-bold;
    color: #072833;
    &.small{
      font-size: 32px;
      line-height: 32px;
      @media screen and (max-width: 430px){
        font-size: 27px;
        line-height: 29px;
      }
    }
    &.big{
      //order: 1;
      font-size: 48px;
      line-height: 48px;
      @media screen and (max-width: 430px){
        font-size: 39px;
        line-height: 43px;
      }
    }


  }
  &__inner-slider-btn-share{
    display: flex;
    justify-content: space-between;
  }
  &__inner-slider-btn-width{
    max-width: 152px;
  }
  &__inner-slider-btn{
    position: relative;
    z-index: 21;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    width: 100%;
    order: 10;
    button,a{
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      width: 100%;
      height: 44px;
      border-radius: 40px;
      background-color: #8BC540;
      color: #fff;
      font-size: 20px;
      line-height: 28px;
      font-family: $ff-base;
      border: none;
      &:not(:first-child){
        margin-top: 24px;
      }
      &:hover {
        background-color: #81b63b;
      }
      @media screen and (max-width: 430px){
        font-size: 17px;
        height: 55px;
      }
    }



  }
  &__inner-slider-btn-type{
    width: 100%;
    &:not(:first-child){
      margin-top: 24px;
    }

  }
  &__inner-slider-des{
    margin-top: 24px;
    font-size: 20px;
    line-height: 28px;
    font-family: $ff-medium;
  }
  &__inner-slider-arrow{
    cursor: pointer;
    position: absolute;
    top: 0;
    width: 30%;
    height: 100%;
    //background-color: red;
    z-index: 30;
    opacity: 0;
  }
  &__inner-slider-arrow-left{
    left: 0;
  }
  &__inner-slider-arrow-right{
    right: 0;
  }
  &__inner-slider-item-plug{
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: rgba(0, 0, 0, 0.11);
  }
  &__inner-slider-video{
    width: 100%;
    position: relative;
    z-index: 10;
    &:before{
      content: "";
      display: block;
      padding-top: 70.25%;
    }
    &[for-video="up"]{
      order: 1;
    }
    &[for-video="down"]{
      order: 5;
      margin: 30px auto 10px;
    }

    video{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
    }
  }
  &__inner-slider-paggination{
    display: none;
    margin-left: -2px;
    margin-right: -2px;
    position: absolute;
    top: 15px;
    z-index: 100;
    width: 94%;
    left: 50%;
    transform: translateX(-49%);
    @media screen and (max-width: 520px){
      //top: 35px;
    }
  }
  &__inner-slider-paggination-item{
    position: relative;
    overflow: hidden;
    margin-left: 2px;
    margin-right: 2px;
    flex-grow: 1;
    background-color: #9CA9AD;
    border-radius: 8px;
    height: 4px;
    border: 1px solid #9CA9AD;
    will-change: transform;
    &.active{
      &:before{
        will-change: width;
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: #fff;
        animation: width 12s linear;
        animation-iteration-count:1;
      }
    }
    &.active-once{
      &:before{
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background-color: #fff;
      }
    }
  }
  &__inner-slider-empty-area{
    position: absolute;
    width: 40%;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    height: 90%;
    z-index: 20;
    @media screen and (max-width: 1024px){
      display: none;
    }
  }
  &__inner-slider-empty-area-mobile{
    display: none;
    position: absolute;
    width: 56%;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    height: 90%;
    z-index: 20;
    @media screen and (max-width: 1024px){
      display: block;
    }
  }
  &__share{
    //display: none;
    //display: block; // ,      
    position: absolute;
    bottom: 50px;
    right: 33px;
    width: 48px;
    height: 48px;
    border-radius: 50%;
    background-color: #fff;
    z-index: 31;
    @media screen and (max-width: 520px){
      bottom: 30px;
    }
    .ya-share2__list.ya-share2__list_direction_horizontal > .ya-share2__item:last-child{
      opacity: 1;
    }
    .ya-share2__container_size_s .ya-share2__item_more.ya-share2__item_has-pretty-view .ya-share2__link_more.ya-share2__link_more-button-type_short{
      width: 48px;
      height: 48px;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
    .ya-share2__item_more.ya-share2__item_has-pretty-view .ya-share2__icon_more{
      background-size: contain;
      height: 17px;
      width: 26px;
      background-repeat: no-repeat;
      background-position: center;
      background-image: url("../img/story-share-icon.svg");
    }
  }
  &__share-down{
    bottom: 33px;
  }
}
@keyframes width {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
@-webkit-keyframes fadeInStr {
  0% {opacity: 0;}
  100% {opacity: 1;}
}
@keyframes fadeIn {
  0% {opacity: 0;}
  100% {opacity: 1;}
}

@keyframes crutch {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1);
  }
}
