.modal {
    background: transparent;
    left: 0;
    height: 100vh;
    height: calc(var(--vh, 1vh) * 100);
    position: fixed;
    top: 0;
    visibility: hidden;
    transition: .3s all ease-in-out;
    width: 100vw;
    z-index: 1111;

    &__closer {
        right: 1.5rem;//24px
        padding: 0;
        position: absolute;
        top: 1.5rem;//24px

        @include respondTo(tablet) {
            right: 1.25rem;//20px
            top: 1.25rem;//20px
        }

        @include respondTo(mobile) {
            border-width: 1px;
            right: .75rem;//12px
            top: .75rem;//12px

            svg {
                width: .75rem;//12px
            }
        }

        svg {
            fill: $color-text-gray-2;
            transition: .3s fill;
        }

        &:hover {
            border-color: $color-text-gray-3;

            svg {
                fill: $color-text-gray-3;
            }
        }
    }

    &__wrapper {
        background-color: $color-bg-white;
        border-radius: 8px;
        box-shadow: 0px 10px 30px rgba(202, 204, 223, 0.5);
        visibility: hidden;
        position: fixed;
        left: 50%;
        top: -520px;
        transform: translate(-50%, -50%);
        max-height: calc(100vh - 2rem);
        max-width: 44.25rem;//708px
        padding: 5rem;//80px
        text-align: center;
        transition: .3s all ease-in-out;
        width: calc(100% - 2.5rem);//40px
        z-index: 1002;

        @include respondTo(desktop) {
            overflow-y: auto;
        }

        @include respondTo(tablet) {
            border-radius: 8px;
            max-width: 40rem;//640px
            padding: 4rem;//64px
        }

        @include respondTo(mobile) {
            border-radius: 6px;
            padding: 2rem 1.5rem 2rem;//32px 24px 32px
        }
    }

    &.open {
        background: rgba(28, 27, 40, 0.4);
        visibility: visible;
        display:block;

        .modal__wrapper {
            visibility: visible;
            top: 50%;
        }
    }

    &__title {
        display: block;
        font-family: $ff-semi-bold;
        font-size: 2.5rem;//40px
        letter-spacing: -0.015em;
        line-height: 1;
        margin-bottom: 2rem;//32px
        text-align: center;

        @include respondTo(tablet) {
            font-size: 2.5rem;//40px
            letter-spacing: -0.01em;
            margin-bottom: 1.5rem;//24px
        }

        @include respondTo(mobile) {
            font-size: 2rem;//32px
            letter-spacing: -0.015em;
        }
    }

    &__main-link {
        align-items: center;
        border: 1px solid $color-primary;
        border-radius: 40px;
        display: inline-flex;
        height: 3.75rem;//60px
        margin: 0 auto 1.75rem;//28px
        padding: .75rem 1.25rem .75rem .75rem;//12px 20px 12px 12px

        @include respondTo(tablet) {
            height: 2.875rem;//46px
            margin-bottom: 1.5rem;//24px
            padding: .5rem 1rem .5rem .5rem;//8px 16px 8px 8px
        }

        @include respondTo(mobile) {
            height: 2.75rem;//44px
            padding: .5rem 1.25rem .5rem .5rem;//8px 20px 8px 8px
        }

        svg {
            margin-right: 1rem;//16px

            @include respondTo(tablet) {
                height: 1.5rem;//24px
                margin-right: .75rem;//12px
                width: 1.5rem;//24px
            }
        }

        span {
            font-family: $ff-semi-bold;
            font-size: 2rem;//32px
            letter-spacing: -0.01em;
            line-height: 1;
            white-space: nowrap;

            @include respondTo(tablet) {
                font-size: 1.625rem;//26px
            }

            @include respondTo(mobile) {
                font-size: 1.5rem;//24px
                letter-spacing: -0.015em;
            }
        }
    }

    &__desc {
        display: block;
        font-family: $ff-medium;
        font-size: 1.25rem;//20px
        line-height: 1.4;
        margin-bottom: 1rem;//16px

        @include respondTo(tablet) {
            font-size: 1rem;//16px
            line-height: 1.5;
            margin-bottom: .75rem;//12px
        }

        @include respondTo(mobile) {
            letter-spacing: -0.015em;
            line-height: 1.25;
            margin-bottom: .5rem;
        }
    }

    &__row {

        & .special {
            display: none;
        }

        & .prog {
            display: none;
        }

        &:not(:first-of-type) {
            margin-top: .75rem;//12px
        }
    }

    &__bottom {
        align-items: flex-start;
        display: flex;
        justify-content: space-between;
        margin-top: 1rem;//16px

        @include respondTo(mobile) {
            display: block;
            margin-top: .75rem;//12px
            text-align: start;
        }
    }

    &__checkbox {
        display: flex;
        padding: .25rem;

        @include respondTo(mobile) {
            margin-bottom: .75rem;//12px
            padding: 0;
        }

        .checkbox {
            margin-top: .125rem;//2px
        }
    }

    &__checkbox-text {
        color: $color-text-gray;
        font-size: .875rem;
        line-height: 1.29;
        margin-left: .5rem;
        text-align: start;

        a {
            color: $color-text-accent;
        }
    }

}
.body-modal{
  /* when modal active */
  touch-action: none;
  -webkit-overflow-scrolling: none;
  overflow: hidden;
  /* Other browsers */
  overscroll-behavior: none;
  margin-right: 16px;
  @include respondTo(tablet) {

  }
  @media screen and (max-width: 1024px){
    margin-right: 0;
  }
}

