.action-type{
  display: flex;
  align-items: center;
  &__icon{
    max-width:12px;
    margin-right: 5px;
  }
  &__des{
    text-transform: uppercase;
    font-family: $ff-bold;
    font-size: 0.75rem;
    color: $color-text-gray;
    letter-spacing: 1.5px;
    line-height: 1.33;
    margin: 0 0.375rem 0.5rem;
  }
}
