.test-n {
  background-color: $color-bg-black;
  border-radius: 1.25rem;
  margin: 0 auto;
  max-width: 57.25rem;
  padding: 3.5rem;

  @include respondTo(mobile) {
    padding: 2rem 1.75rem 2.5rem;
  }

  &_white {
    background-color: $color-bg-white;
  }

  &_gray {
    background-color: $color-bg-gray;
  }

  &__title {
    color: $color-text-white;
    display: block;
    font-family: $ff-semi-bold;
    font-size: 1.5rem;
    line-height: 1.08;

    @include respondTo(mobile) {
      font-size: 1.25rem;
      line-height: 1.1;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }
  }

  &__description {
    color: $color-text-white;
    display: block;
    font-size: 1.25rem;
    line-height: 1.4;
    margin-top: 1rem;

    @include respondTo(mobile) {
      font-size: 1rem;
      letter-spacing: -0.015em;
      line-height: 1.25;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }

    p {
      color: $color-text-white;
      font-size: 1.25rem;
      line-height: 1.4;

      @include respondTo(mobile) {
        font-size: 1rem;
        letter-spacing: -0.015em;
        line-height: 1.25;
      }

      .test-n_white &, .test-n_gray & {
        color: $color-text;
      }
    }
  }

  &__answer {
    border-top: 1px solid rgba(202, 204, 223, 0.5);
    display: flex;
    flex-direction: column;
    gap: 1.25rem;
    margin-bottom: 2rem;
    padding-top: 2.5rem;

    @include respondTo(mobile) {
      margin-bottom: 1.5rem;
      padding-top: 2rem;
    }
  }

  &__answer-title {
    color: $color-text-white;
    display: block;
    font-family: $ff-semi-bold;
    font-size: 1.5rem;
    line-height: 1.08;

    @include respondTo(mobile) {
      font-size: 1.25rem;
      letter-spacing: -0.015em;
      line-height: 1.1;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }
  }

  &__answer-body {
    color: $color-text-white;
    display: block;
    font-family: $ff-medium;
    font-size: 1.25rem;
    line-height: 1.4;

    @include respondTo(mobile) {
      font-size: 1rem;
      letter-spacing: -0.015em;
      line-height: 1.25;
    }

    .test-n_white &, .test-n_gray & {
      color: $color-text;
    }

    p {
      color: $color-text-white;
      font-family: $ff-medium;
      font-size: 1.25rem;
      line-height: 1.4;

      @include respondTo(mobile) {
        font-size: 1rem;
        letter-spacing: -0.015em;
        line-height: 1.25;
      }

      .test-n_white &, .test-n_gray & {
        color: $color-text;
      }
    }

    a {
      color: $color-card-green;
    }
  }

  &__footer {
    margin-top: 2.5rem;

    @include respondTo(mobile) {
      margin-top: 2rem;
    }
  }

  &__controls {
    display: flex;
    gap: 1rem;

    @include respondTo(mobile) {
      align-items: flex-start;
      flex-direction: column;
    }
  }
}

.test-n-wrap {

  &:not(:last-of-type) {
		margin-bottom: 2rem;

		@include respondTo(mobile) {
			margin-bottom: 1.5rem;
		}
	}
}
