body{
  @media print {
    background-color: hsla(0,0%,100%,.8);
  }
}
.page-title-block__headline-width{
  margin-right: 2.25rem;
  width: calc(100% - 4rem);
}
.activation-certificates-crediting{
  width: 100%;
  padding: 64px 104px;
  max-width: 1228px;
  background: #fff;
  border-radius: 20px;
  margin: auto;
  .check-status {
    padding: 0;
    width: 100%;
    @media screen and (max-width: 1243px){
      margin: 0;
    }
  }
  .check-status__form-wrapper{
    .check-status__pin-input-error{
      input{
        color: red;
      }
    }
    &.check-status__final-window{
      .check-status__button{
        margin-top: 32px;
        @media screen and (max-width: 880px){
          margin-top:24px;
        }
      }
    }

    .check-status__input{
      font-family: $ff-base;
      margin-bottom: 0;
      &.pin{
        text-align: center;
      }
    }
    .check-status__button{
      margin-top: 32px;
      width: max-content;
      &.unactive{
        background-color: #CACCDF;
        border-color: #CACCDF;
      }
      @include respondTo(desktop) {
        margin-bottom: 0;
      }

      @media screen and (max-width: 420px){
        width: 100%;
      }
    }
    .check-status__hint{
      margin-bottom: 0;
      &.error{
        margin-top: 12px;
        margin-bottom: 0;
      }
      &.reference{
        margin-top: 12px;
      }
    }
    .check-status__prompt{
      margin-top: 8px;
      span{
        margin-left: 4px;
      }
    }
    .check-status__text.bigger{
      margin-bottom: 0;
      font-family: $ff-bold;
      i{
        font-style: normal;
        @include respondTo(mobile) {
          display: none;
        }
      }
    }
    .check-status__card-wrapper {
      @media screen and (max-width: 420px){
        max-width: 100%;
      }

    }
  }
  .check-status__card-wrapper{
    .card-list{
      margin: 1rem -0.625rem -1.25rem;
    }
  }
  .check-status__container{
    display: grid;
    grid-template-columns: minmax(200px,525px) minmax(100px,292px);
    grid-column-gap: 20px;
    grid-row-gap: 32px;
    @media screen and (max-width: 880px){
      grid-template-columns: 1fr;
    }
  }
  .check-status__form-wrapper,.check-status__card-wrapper{
    flex: none;
  }
  @include respondTo(mobile) {
    padding: 48px 20px;
  }


}
.check-status{
  background-color: #fff;
  &__footnote{
    margin-top: 8px;
    p{
      font-size: 14px;
      line-height: 20px;
      color: #686D82;
    }
  }
  &__time-action{
    margin-top: 20px;
    .check-status__text{
      &:not(:first-child){
        margin-top: 8px;
      }
    }
  }
  &__time-action-value{
    margin-left: 5px;
  }
  &__final-window{
    display: flex;
    flex-direction: column;
  }
  &__tabs{
    display: flex;
    width: 100%;
    max-width: 525px;
    margin-bottom: 48px;
    justify-content: space-between;
    @media screen and (max-width: 560px){
      display: none;
    }
  }
  &__tabs-item{
    cursor: pointer;
    &:after{
      content:"";
      display: block;
      width: 0;
      height: 2px;
      background-color: #8BC540;
      transition-duration: 0.5s;
      margin: 16px auto 0;
    }
    &.active{
      &:after{
        width: 100%;
      }
    }
    p{
      color: #1C1B28;
      letter-spacing: 1.5px;
      text-transform: uppercase;
      font-size: 12px;
      line-height: 20px;
    }
  }
  &__status{
    margin-top: 20px;
    font-size: 22px;
    line-height: 26px;
    color: #67A511;
    font-family: $ff-bold;
    &.error{
      color:#ff4542;
    }
  }
  &__user-type{
    display: none;
    &.active{
      display: block;
    }
  }
  &__employee-information{
    font-size: 18px;
    line-height: 26px;
    color: #1C1B28;
    margin-top: 20px;
    @include respondTo(mobile) {
      font-size: 16px;
      line-height: 24px;
      margin-top: 16px;
    }
    @include respondTo(small) {
      font-size: 16px;
      line-height: 24px;
    }
    a{
      color: #67A511;
    }
  }

  &__tabs-item{
  }
  &__tabs-mobile{
    display: none;
    margin-bottom: 48px;
    position: relative;
    width: 100%;
    max-width: 100%;
    &.active{
      .check-status__tabs-mobile-icon{
        transform: rotate(180deg);
      }
    }
    @media screen and (max-width: 560px){
      display:block;
    }
  }
  &__tabs-mobile-btn{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 12px;
    background-color  : #FFFFFF;
    border: 1px solid #CACCDF;
    border-radius: 6px;
    p{
      font-size: 16px;
      line-height: 20px;
      @media screen and (max-width: 360px){
        font-size: 13px;
      }
    }
  }
  &__tabs-mobile-list{
    max-height: 0;
    overflow: hidden;
    -webkit-transition-duration: .5s;
    -o-transition-duration: .5s;
    transition-duration: .5s;
    position: absolute;
    width: 100%;
    top: 47px;
    left: 0;
    padding: 0px 12px;
    background-color  : #FFFFFF;
    border: 1px solid #CACCDF;
    border-radius: 6px;
    opacity: 0;
    li{
      cursor: pointer;
      font-size: 14px;
      &:first-child{
        padding-top: 10px;
      }
      &:nth-child(2){
        padding-bottom: 10px;
      }
      &:not(:first-child){
        margin-top: 15px;
      }
    }
  }
  &__tabs-mobile-icon{
    transition-duration: .5s;
  }
  &__wrapper-input{
    display: grid;
    grid-template-columns: minmax(95px,220px) minmax(95px,220px);
    grid-column-gap: 12px;
    @include respondTo(small) {
      grid-template-columns: minmax(94px,94px) 1fr;
    }
  }
  &__participation-condition{
    p{
      font-family: $ff-bold;
      color: #ff4542;
      margin-top: 10px;
    }

  }
}

