.container-print{
  position: relative;
  z-index: 10;
  margin: 0 auto;
  max-width: 1026px;
  //padding: 0 10px;
  display: flex;
  flex-direction: column;
  background-color: hsla(0,0%,100%,.8);
  //min-height: 97vh;
  @media print {
    height: 297mm;
    width: 210mm;
    background-color: #fff;
    -webkit-print-color-adjust: exact;
    color-adjust: exact;
  }
}
.print{
  @media print {
    display: none;
  }
}
.certificate-print{
  p{
    color: #072833;
  }
  position: relative;
  width: 100%;
  padding-top: 68px;
  @media print {
    padding-top: 0px;
  }
  .margin{
    margin-top: 20px;
  }
  .border{
    border-bottom: 1px solid #9BA9AD;
  }
  &__patterns-up{
    position: fixed;
    top: 0;
    right: 0;
  }
  &__patterns-down{
    position: fixed;
    left: 0;
    bottom: 0;
  }
  &__body{
    width: 100%;
    max-width: 797px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    min-height: calc(100vh - 68px);
    padding-bottom: 30px;

    @media print{
      padding-bottom: 0;
      min-height: auto;
    }
  }
  &__header{
    padding: 0 10px;
  }
  &__text-center{
    text-align: center;
  }
  &__title{
    font-size: 48px;
    line-height: 56px;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-align: center;
    @media print {
      font-size: 28px;
      line-height: 30px;
    }
  }
  &__subtitle{
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    margin-top: 8px;
    @media print {
      font-size: 16px;
      line-height: 20px;
    }
  }
  &__main-part{
    margin-top: 94px;
    margin-bottom: 30px;
    padding: 0 10px;
    @media print {
      margin-top: 35px;
    }
  }
  &__used-font{
    font-size: 16px;
    line-height: 24px;
    font-family: $ff-medium;
  }
  &__name{
    margin-top: 16px;
    letter-spacing: 1px;
    font-size: 44px;
    line-height: 52px;
    text-align: center;
    font-family: $ff-bold;
    @media print {
      margin-top: 16px;
      font-size: 27px;
      line-height: 32px;
    }
  }
  &__block-frames{
    margin-top: 16px;
    padding: 32px 0;
    border-bottom: 1px solid #9BA9AD;
    border-top: 1px solid #9BA9AD;
    @media print {
      padding: 12px 0;
    }
  }
  &__passport-data{
    margin-top: 55px;
    @media print {
      margin-top: 30px;
    }
  }
  &__wrapper{
    width: 100%;
    //max-width: 673px;
    margin: 0 auto;
    @media print {
      margin-top: auto;
    }
  }
  &__title-item{
    font-size: 20px;
    line-height: 28px;
    font-family: $ff-medium;
    margin-bottom: 16px;
  }
  &__passport-data-item {
    display: grid;
    grid-template-columns: minmax(100px, 155px) minmax(100px, 155px) 1fr;
    grid-column-gap: 40px;
  }
  &__data {
    display: flex;

    p{
      font-size: 16px;
      line-height: normal;
      font-family: $ff-medium;
      &:first-child{
        margin-right: 4px;
      }
    }
    &.swap{
      .certificate-print__data-val{
        order: -1;
      }
    }
  }
  &__data-val{
    flex-grow: 1;
    margin-top: -4px;
    p{
      padding-left: 5px;
      border-bottom: 1px solid #9BA9AD;
    }
  }
  &__family-composition{
    margin-top: 67px;
    @media print {
      margin-top: 30px;
    }
  }
  &__payment{
    margin-top: 60px;
    @media print {
      margin-top: 30px;
    }
  }
  &__payment-item{
    &:not(:first-child){
      margin-top: 20px;
      @media print {
        margin-top: 17px;
      }
    }
  }
  &__payment-footnote{
    margin-top: 8px;
    @media print {
      margin-top: 35px;
    }
    p{
      color: #677B82;
      font-size: 12px;
      line-height: 16px;
      font-family: $ff-medium;
    }
  }
  &__signature{
    //width: 100%;
    //max-width: 797px;
    margin-top: auto;
    display: flex;
    padding: 0 10px;
    justify-content: space-between;
    @media print {
      margin-top: 20px;
    }
  }
  &__signature-item{

  }
  &__signature-key{
    //min-height: 55px;
    color: #677B82!important;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.4px;
    font-family: $ff-medium;
    @media print {
      //font-size: 12px;
    }
  }
  &__signature-value-wrap{
    margin-top: 15px;
    border-bottom: 1px solid #677B82;
    display: flex;
    //justify-content: space-between;
  }
  &__signature-footnote{
    padding-top: 2px;
    margin-left: 10px;
    color: #677B82;
    font-size: 18px;
    line-height: 16px;
  }
  &__wrapper-footer{
    position: relative;
    //background-color: rgba(255, 255, 255, 0.8);
    margin-top: auto;
    padding: 0 10px 53px;
    @media print {
      //margin-top: 10px;
      padding-bottom: 0;
    }
  }
  &__serial-number{
    margin: 32px auto 0;
    width: 100%;
    max-width: 345px;
    display: grid;
    grid-template-columns: repeat(2,minmax(120px,155px));
    grid-column-gap: 10px;
    justify-content: space-between;
  }
}

// 
.certificate-breeding{
  //margin-top: 72px;
  display: grid;
  grid-template-columns: repeat(2,1fr);
  grid-gap: 20px;
  @media screen and (max-width: 800px){
    grid-template-columns: repeat(1,1fr);
  }
  @media screen and (max-width: 500px){
    margin-top: 38px;
  }
  &__item{
    display: flex;
    flex-direction: column;
    background-color: #FFFFFF;
    border-radius: 16px;
    padding: 32px 32px 40px;
    @media screen and (max-width: 500px){
      padding: 14px 14px 21px;
    }
  }
  &__title{
    font-size: 24px;
    line-height: 26px;
    font-family: $ff-bold;
    color: #1C1B28;
    @media screen and (max-width: 500px){
      font-size: 20px;
      line-height: 24px;
    }
  }
  &__des{
    font-size: 18px;
    line-height: 26px;
    margin-top: 12px;
    margin-bottom: 24px;
    @media screen and (max-width: 500px){
      font-size: 16px;
      line-height: 22px;
    }
  }
  &__btn{
    width: max-content;
    margin-top: auto;
  }
  &__block{
    margin-top: 72px;
    .page-title-block__headline_little{
      @include respondTo(mobile) {
        font-size: 26px;
      }
    }
  }
  &__reference-information{
    li{
      a{
        color: #8bc540;
      }
    }
  }

}
.block-question{
  margin-top: 80px;
  @media screen and (max-width: 500px){
    margin-top: 45px;
  }
  .headline-row__title_small{
    font-size: 2.5rem;
    line-height: 1.1;
    @media screen and (max-width: 767px){
      font-size: 2rem;
      letter-spacing: -.015em;
      line-height: 1.12;
    }
  }
}
.block-feedback{
  margin-top: 57px;
  @media screen and (max-width: 500px){
    margin-top: 30px;
  }

}
