.input, .input-text {
    $this: #{&};
    display: block;
    width: 100%;

    input {
        appearance: none;
        background-color: $color-bg-white;
        border: 1px solid transparent;
        border-radius: 8px;
        color: $color-text;
        font-family: $ff-base;
        font-size: 1.125rem;//18px
        height: 3.75rem;//60px
        line-height: 1.44;
        padding: 0 1.25rem;//0 20px
        transition: .3s all;
        width: 100%;

        @include respondTo(tablet) {
            font-size: 1rem;//16px
            height: 3rem;//48px
            line-height: 1.5;
            padding: 0 1rem;//0 16px
        }

        @include respondTo(mobile) {
            border-radius: 6px;
            height: 2.5rem;//40px
            letter-spacing: -0.015em;
            line-height: 1.25;
            padding: 0 .75rem;//0 12px
        }

        &::placeholder {
            color: $color-text-gray-2;
            font-family: $ff-base;
            font-size: 1.125rem;//18px
            line-height: 1.44;

            @include respondTo(tablet) {
                font-size: 1rem;//16px
                line-height: 1.5;
            }

            @include respondTo(mobile) {
                letter-spacing: -0.015em;
                line-height: 1.25;
            }
        }

        &::-moz-placeholder {
            line-height: 3.625rem;//58px

            @include respondTo(tablet) {
                line-height: 2.75rem;//44px
            }

            @include respondTo(mobile) {
                line-height: 2.25rem;//36px
            }
        }

        /* Chrome, Safari, Edge, Opera */
        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button{
            -webkit-appearance: none;
            margin: 0;
        }

        &:focus {
            border-color: $color-primary;
            outline: none;
        }

        &:hover {
            border-color: $color-primary;
        }

        &:disabled{
            color: $color-text-gray-3;
        }
    }

    /* Firefox */
    input[type=number] {
        -moz-appearance: textfield;
    }

    &.input_bordered {

        input {
            border-color: $color-text-gray-2;

            &:focus {
                border-color: $color-primary;
            }

            &:hover {
                border-color: $color-primary;
            }
        }

        &.input_error {

            input {
                border-color: $color-text-red;
            }
        }
    }

    &__error {
        color: $color-text-red;
        display: none;
        font-family: $ff-medium;
        font-size: .875rem;//14px
        line-height: 1.29;
        margin-top: .25rem;//4px
        text-align: start;

        @include respondTo(tablet) {
            font-size: .75rem;//12px
            line-height: 1.33;
        }
    }

    &_error {

        input {
            border-color: $color-text-red;
        }

        .input__error {
            display: block;
        }
    }

    & .lbl{
        display: block;
        font-size: 14px;
        line-height: 1.25;
        margin-bottom: .5rem;
    }

    & .inp{
        border: 1px solid $color-text-gray-2;
    }

  &.input_small input {
    height: 2.375rem;
    padding: .625rem 1rem;
    font-family: $ff-medium;

    &, &::placeholder {
      font-size: .875rem;//14px
      line-height: 1.28;

      @include respondTo(mobile) {
        font-size: .75rem;//12px
        line-height: 1.33;
      }
    }

    @include respondTo(mobile) {
      height: 2.25rem;
    }
  }

  &.input_medium input {
    height: 3rem;
    padding: .625rem 1.25rem;
    border-radius: .75rem;

    @include respondTo(tablet) {
      padding: .875rem 1.25rem;
    }

    @include respondTo(mobile) {
      height: 2.5rem;
      border-radius: .375rem;
    }

    &, &::placeholder {
      font-family: $ff-medium;
      font-size: 1.25rem;
      line-height: 1.4;

      @include respondTo(tablet) {
        font-size: 1rem;
        line-height: 1.5;
      }

      @include respondTo(mobile) {
        letter-spacing: -0.015em;
      }
    }
  }

  &_dark-placeholder {

    input {

      &::placeholder {
        color: $color-text-gray;
      }
    }
  }

  &.disabled {
    pointer-events: none;
  }
}
