.breadcrumbs-new {
  display: flex;
  align-items: center;
  flex-wrap: wrap;

  @include respondTo(mobile) {
    display: none;
  }

  &__link, &__home {
    cursor: pointer;
    color: $color-text-gray;
    font-size: .875rem;
    line-height: 1.28;
    font-family: $ff-medium;

    @include respondTo(mobile) {
      font-size: .75rem;
    }
  }

  &__home {
    flex: none;
    svg {
      vertical-align: middle;
    }
  }

  &__link {
    position: relative;
    padding-left: 1.25rem; //20px

    &::before {
      content: '';
      position: absolute;
      left: .5rem;
      top: 50%;
      margin-top: -0.125rem;
      width: .25rem;
      height: .25rem;
      border-radius: 50%;
      background: $color-text-gray;
    }
  }

  &__link_current {
    color: #A0A5B4;
    cursor: default;

    &:hover {
      color: #A0A5B4;
    }
  }

  &.with-mt {
    margin-top: 1.5rem;
  }

  &.show-always {
    @include respondTo(mobile) {
      display: flex;
    }
  }
}
