.media-menu-btn {
  position: absolute;
  left: calc(50% + 43.375rem);
  transition: opacity .3s,visibility .3s;
  top: 1.25rem;
  z-index: 11;
  opacity: 0;
  visibility: hidden;
  &.show {
    opacity: 1;
    visibility: visible;
  }
  @media screen and (max-width: 1504px) {
    left: auto;
    right: 0.8rem;
    bottom:1.313rem;
  }
  @media screen and (max-width: 1364px) {
    left: auto;
    right:0.825rem;
    bottom:1.313rem;
  }
  @include respondTo(desktop) {
    right:0.725rem;
    bottom:1.313rem;
    top: auto;
    opacity: 1;
    visibility: visible;
  }
  @include respondTo(mobile) {
    right: 20px;
    bottom: 26px;
  }
  @include respondTo(small) {
    right: 20px;
    bottom:14px;
  }
}
.header-buttons-main{
  .header-buttons__connection{
    @media screen and (max-width: 1504px) {
      left: auto;
      right:4.125rem;
      bottom:1.313rem;
    }
    @media screen and (max-width: 1364px) {
      right:4.125rem;
      bottom:1.313rem;
    }
    @include respondTo(desktop) {
      right:4.125rem;
      bottom:1.313rem;
    }
    @include respondTo(mobile) {
      right: 60px;
      bottom: 26px;
    }
    @include respondTo(small) {
      right: 60px;
      bottom:14px;
    }



  }
}
.block-banners{
  .card-service__circle{
    transition-duration: .5s;
  }
  .card-list__col{
    &:hover .card-service__circle{
      transform: scale(1.1);
    }
  }
}

.header-docs {
  @include respondTo(tablet) {
