.textarea {
    display: block;
    width: 100%;

    textarea {
        background-color: $color-bg-white;
        border: 1px solid transparent;
        border-radius: 8px;
        color: $color-text;
        font-family: $ff-base;
        font-size: 1.125rem;//18px
        height: 14.125rem;//226px
        line-height: 1.44;
        padding: 1.5rem 1.25rem;//24px 20px
        resize: none;
        transition: .3s all;
        width: 100%;

        @include respondTo(tablet) {
            border-radius: 8px;
            font-size: 1rem;//16px
            height: 12.5rem;//200px
            line-height: 1.5;
            padding: .75rem 1rem;//12px 16px
        }

        @include respondTo(mobile) {
            border-radius: 6px;
            letter-spacing: -0.015em;
            line-height: 1.25;
            padding: .75rem;//12px
        }

        &::placeholder {
            color: $color-text-gray-2;
            font-family: $ff-base;
            font-size: 1.125rem;//18px
            line-height: 1.44;

            @include respondTo(tablet) {
                font-size: 1rem;//16px
                line-height: 1.5;
            }

            @include respondTo(mobile) {
                letter-spacing: -0.015em;
                line-height: 1.25;
            }
        }

        &:focus {
            border-color: $color-primary;
            outline: none;
        }

        &:hover {
            border-color: $color-primary;
        }
    }

    &__error {
        color: $color-text-red;
        display: none;
        font-family: $ff-medium;
        font-size: .875rem;//14px
        line-height: 1.29;
        margin-top: .25rem;//4px
        text-align: start;

        @include respondTo(tablet) {
            font-size: .75rem;//12px
            line-height: 1.33;
        }
    }

    &_error {
        
        textarea {
            border-color: $color-text-red;
        }

        .textarea__error {
            display: block;
        }
    }

    &_bordered {

        textarea {
            border-color: $color-text-gray-2;

            &:focus {
                border-color: $color-primary;
            }

            &:hover {
                border-color: $color-primary;
            }
        }

        &.textarea_error {

            textarea {
                border-color: $color-text-red;
            }
        }
    }
}