    //  display: none!important;
    //}
    //.test-answer,.test-cotainer-answer{
    //  display: none!important;
    //}
    //.test-cotainer-final-result, .test-window__com{
    //  display: flex;
    //}

  }
  &.result{
    //.test-answer,.test-cotainer-answer {
    //  display: none;
    //}
    //.test-answer {
    //  display: none;
    //}
    //.test-cotainer-result-answers {//
    //  display: flex;
    //}
  }
  &.correct, &.uncorrect {


    &.correct {
      .test-window__response-status {
        &.correct {
          display: flex;
        }

        &.uncorrect {
          display: none;
        }
      }
    }

    &.uncorrect {
      .test-window__response-status {
        &.correct {
          display: none;
        }

        &.uncorrect {
          display: flex;
        }
      }
    }


  }

}
.test-window__com,.test-cotainer-final-result{
  //display: none;
  //padding: 3.875rem 2rem 1.875rem;
  display: flex;
  //padding: 0 2rem 1.875rem;
  //margin-top: 1.25rem;

}
.test-window__com{
  padding-bottom: 1.87rem;
}
.test-cotainer-result-answers{
  //min-height: 460px;
  flex-direction: column;
  display: none;//
  //display: flex;
  &.uncorrect{
    .test-window__response-status.correct{
      display: none;
    }
    .test-window__response-status.uncorrect{
      display: flex;
    }
  }
  &.correct{
    .test-window__response-status.correct{
      display: flex;
    }
    .test-window__response-status.uncorrect{
      display: none;
    }
  }
}
.test-cotainer-result-emty-block{
  display: none;
  @include respondTo(small) {
    display: block;
  }
}
.test-window{
  &__response-status-icon{
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  &__response-status-des{
    color: $color-text;
    font-family: $ff-bold;
    font-size: 1.5rem;
    margin-top: 1.25rem;
  }
  &__response-correct-answer{
    @include respondTo(small) {
      margin-bottom: 60px;
      margin-top: 1.562rem;
      max-height: 400px;
      overflow: auto;
    }
    margin-top: 3.562rem;
    text-align: center;
    font-size: 20px;
    font-family: $ff-medium;
  }
  &__response-des{
    a{
      color: #8bc540;
      font-family: $ff-medium;
    }
    &:first-child{
      //margin-top: 1rem;
    }
    text-align: center;
    &:not(:first-child){
      margin-top: 0.3rem;
    }
  }
}
