.subscription {
    margin: 0 auto;
    max-width: 37.75rem;//604px

    @include respondTo(tablet) {
        max-width: 29.625rem;//474px
    }

    &__title {
        margin-bottom: 2rem;//32px
        white-space: nowrap;

        @include respondTo(mobile) {
            text-align: center;
            white-space: normal;
        }
    }

    &__desc {
        color: $color-text-gray;
        display: block;
        font-size: 1.125rem;//18px
        line-height: 1.44;
        margin-bottom: 2rem;//32px
        text-align: center;

        @include respondTo(tablet) {
            font-size: 1rem;//16px
            line-height: 1.5;
            margin-bottom: 1.25rem;//20px
        }

        @include respondTo(mobile) {
            font-size: .75rem;//12px
            line-height: 1.33;
            margin-bottom: 1rem;//16px
        }
    }

    &__input {
        margin-bottom: .75rem;//12px
        position: relative;

        & .special {
            display: none;
        }

        input {
            padding-right: 9.25rem;
          &.subscription-form-submited{
            &:-moz-placeholder { color: #8cc541; }
            &::-webkit-input-placeholder { color: #8cc541;}
          }

            @include respondTo(tablet) {
                padding-right: 8.25rem;
            }

            @include respondTo(mobile) {
                padding: 0 3.25rem 0 1rem;//0 52px 0 16px
            }
        }
    }

    &__submit {
        background-color: transparent;
        border: none;
        cursor: pointer;
        font-family: $ff-bold;
        font-size: .75rem;
        letter-spacing: 1.5px;
        padding: .25rem 0;//4px 0
        position: absolute;
        right: 1.25rem;//20px
        text-transform: uppercase;
        top: 1.1875rem;//19px
        transition: .3s color;

        svg {
            display: none;
            fill: $color-icon;
            transition: .3s fill;
        }

        @include respondTo(tablet) {
            right: 1rem;//16px
            top: .8125rem;//13px
        }

        @include respondTo(mobile) {
            align-items: center;
            display: flex;
            height: 1.5rem;//24px
            justify-content: center;
            padding: 0;
            top: .5rem;//8px
            width: 1.5rem;//24px

            span {
                display: none;
            }

            svg {
                display: block;
            }
        }

        &:focus {
            outline: none;
        }

        &:hover {
            color: $color-primary;

            svg {
                fill: $color-text-accent;
            }
        }
    }

    &__checkbox {
        align-items: center;
        display: flex;
        padding: .25rem;

        @include respondTo(mobile) {
            align-items: flex-start;
        }
    }

    &__checkbox-text {
        color: $color-text-gray;
        font-size: .875rem;//14px
        line-height: 1.29;
        margin-left: .5rem;

        @include respondTo(tablet) {
            font-size: .75rem;//12px
            line-height: 1.33;
        }

        @include respondTo(mobile) {
            margin-left: .25rem;//4px
        }

        a {
            color: $color-text-accent;
            display: inline-block;
        }
    }

    &__success {
        align-items: center;
        background-color: $color-bg-white;
        border-radius: 12px;
        bottom: 1rem;//16px
        box-shadow: 0px 10px 30px rgba(202, 204, 223, 0.5);
        display: flex;
        margin-left: 1rem;//16px
        opacity: 0;
        padding: 1.5rem;//24px
        pointer-events: none;
        position: fixed;
        visibility: hidden;
        right: 1rem;//16px
        transition: .3s all;
        z-index: 100;

        svg {
            margin-right: .75rem;//12px
            min-width: 1.5rem;//24px
        }

        span {
            display: block;
            font-family: $ff-medium;
            font-size: .875rem;//14px
            line-height: 1.29;
          a{
            color: $color-text-accent;
          }
        }

        &.show {
            opacity: 1;
            pointer-events: auto;
            visibility: visible;
        }
    }

    & + .banner {
        margin-top: 4rem;//64px
    }
}
