.tab{
  &__nav{
    display: flex;
    align-items: center;
    margin-bottom: 1.25rem; //20px
  }

  &__nav-item{
    @extend %btn-reset;

    font-family: $ff-bold;
    font-size: .75rem; //12px
    line-height: 1.3;
    white-space: nowrap;
    border-radius: 8px;
    height: 2rem; //32px
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: .5rem 1rem;
    color: $color-text;
    background-color: $color-card;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    transition: background-color ease .33s;

    @include respondTo(mobile){
      font-size: .625rem; //10px
      line-height: 1.2;
      padding: .5rem .75rem; //8px 12px
      height: 1.75rem; //28px
    }

    &:not(:last-child){
      margin-right: .75rem; //12px

      @include respondTo(mobile){
        margin-right: .5rem; //8px
      }
    }

    &.is-active{
      pointer-events: none;
      background-color: $color-card-yellow;
    }
  }

  &__content{
    border-top: 1px solid $color-text-gray-2;
    display: none;

    &.is-show{
      display: block;
    }
  }
}
