.breadcrumbs{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    &:not(:last-child):hover{
      text-shadow: 0 0 1px #9e9d9d;
    }
    a{
      font-size: 0.875rem;
      font-family: $ff-base;
      color: $color-text;
    }
    &:not(:last-child){
      &:after{
        content: ">";
        margin: 0rem .313rem 0;
      }
    }
    &:last-child{
      a{
        font-family: $ff-bold;
      }

    }
  }

}
