.btn {
    $this: &;

    @extend %btn-reset;

    align-items: center;
    border: 1px solid $color-primary;
    border-radius: 40px;
    display: inline-flex;
    font-family: $ff-base;
    font-size: 1.25rem;//20px
    height: 2.75rem;//44px
    min-width: fit-content;
    line-height: 1.625rem;//26px
    padding: .5rem 1.25rem;//8px 20px
    transition: .3s background-color, .3s border-color, .3s color;
    justify-content: center;

    &:hover {
        background-color: $color-primary;
    }

    @include respondTo(tablet) {
        font-size: 1rem;//16px
        height: 2.5rem;//40px
        line-height: 1.25rem;//20px
    }

    @include respondTo(mobile) {
        height: 2.25rem;//36px
        letter-spacing: -0.015em;
        line-height: 1rem;//16px
    }

    &[disabled] {
        color: $color-text-gray-2;
        border-color: $color-text-gray-2;
        pointer-events: none;

        &:hover {
            background-color: transparent;
        }
    }

    &.disabled {
        color: $color-text-gray-2;
        border-color: $color-text-gray-2;
        pointer-events: none;

        &:hover {
            background-color: transparent;
        }
    }

    &_big {
        height: 4rem;//64px
        line-height: 1.4;
        padding: 1.125rem 1.25rem;//18px 20px

        @include respondTo(tablet) {
            height: 3rem;//48px
            line-height: 1.75;
            padding: .75rem 1.25rem;//12px 20px
        }

        @include respondTo(mobile) {
            height: 2.25rem;//36px
            line-height: 1.25;
            padding: .5rem 1.25rem;//8px 20px
        }
    }

  &_small {
    height: 2.125rem;
    font-size: .875rem;
    line-height: 1.26;
    border: 2px solid $color-primary;
    font-family: $ff-medium
  }

  &_medium {
    height: 2.75rem;
    font-size: 1.25rem;
    line-height: 1.4;
    font-family: $ff-medium;
    border: 2px solid $color-primary;

    @include respondTo(tablet) {
      height: 2.5rem;
      font-size: 1rem;
      line-height: 1.5;
      letter-spacing: 0;
    }

    @include respondTo(mobile) {
      font-size: 1rem;
      height: 2.25rem;
      line-height: 1.25;
      letter-spacing: -0.015em;
    }
  }

  &_half-medium {
    border-width: 2px;
    font-family: $ff-medium;
    font-size: 1.125rem;
    height: 3rem;
    line-height: 1.56;
    padding: .625rem 1.25rem;

    @include respondTo(tablet) {
      font-size: 1rem;
      height: 2.25rem;
      letter-spacing: -0.015em;
      line-height: 1.25rem;
      padding: .5rem 1.25rem;
    }

    &.disabled {
      background: transparent;
    }

    &[disabled] {
      background: transparent;
    }
  }

    &_green {
        background-color: $color-primary;
        color: $color-text-white!important;

        & .upload-video {
            margin-left: 12px;
        }
    }

    &_green-with-hover {
      background-color: $color-primary;
      color: $color-text-white;

      &:hover {
        background-color: transparent;

        .test-n_white &, .test-n_gray & {
          color: $color-text;
        }
      }
    }

    &#{$this}_green-with-hover {
      color: $color-text-white;

      &.disabled {
        color: $color-text-gray-2;
      }

      &[disabled] {
        color: $color-text-gray-2;
      }

      &:hover {

        .test-n_white &, .test-n_gray & {
          color: $color-text;
        }
      }
    }

    &_green-n-black {
        background-color: $color-primary;
        color: $color-text;

        &:hover {
            color: $color-text-white;
        }
    }
  &_green-n-white {
    background-color: $color-primary;
    color: $color-text-white!important;

    &:hover {
      background-color: $color-text-white;
      color: $color-text;
    }
  }
  &_gray-bor-black {
    background-color: transparent;
    border: 1px solid #CACCDF;
    color: $color-text!important;

    &:hover {
      background-color: $color-text-white;
      color: $color-text;
    }
  }

    &_gray {
        background-color: $color-text-gray-3;
        border-color: $color-text-gray-3;
        color: $color-text-white!important;

        &:hover {
            background-color: $color-text-gray-3;
        }
    }

    &.full {
        width: 100%;
    }

  &_dark-gray {
    background-color: transparent;
    border-color: $color-text-gray-2;
    color: $color-text-gray-2!important;

    &:hover {
      background-color: transparent;
      border-color: $color-primary;
      color: $color-primary;
    }
  }
}
