.soc-link{
    display: flex;
    align-items: center;

    &__item{
        width: 1.75rem; //28px
        height: 1.75rem; //28px

        &:not(:last-of-type){
            margin-right: 1rem;
        }
    }

    &__icon{
        fill: $color-text-gray;
        transition: fill ease .33s;

        &:hover{
            fill: #ffffff;
        }
    }
}
