.courses-section{
  .polls{
    border-radius: 20px;
    background-color: #1C1B28;
    padding: 56px;
    margin-top: 40px;
    margin-bottom: 50px;
    p,label{
      color: #fff;
    }
    button{
      color: #CACCDF;;
    }
    @include respondTo(mobile) {
      padding: 40px;
    }
    @include respondTo(small) {
      padding: 20px;
    }
  }
}

.polls{
  &__main-container{
    margin-top: 30px;
    padding: 56px;
    border-radius: 20px;
    @media screen and (max-width: 720px){
      padding: 15px;
    }
    &.black{
      background-color: #1C1B28;
      p,label{
        color: #fff;
      }
      button{
        color: #CACCDF;
      }
  }
    &.white{
      background-color: #fff;
    }
    &.gray{
      background-color: #F1F2F4;
    }

  }
  &__window{
    position: relative;
  }
  &__call-action{
    font-family: $ff-bold;
    font-size: 24px;
    line-height: 26px;
    margin-bottom: 24px;
    @include respondTo(small) {
      font-size: 20px;
    }
  }
  &__component-question{
    margin-top: 24px;
  }
  &__question{
    font-family: $ff-bold;
    font-size: 20px;
    line-height: 28px;
    @media screen and (max-width: 620px){
      font-size: 17px;
      line-height: 22px;
    }
    @include respondTo(small) {
      font-size: 15px;
      line-height: 18px;
    }
  }
  &__list-answers{
    margin-top: 16px;
  }
  &__list-answers-item{
    position: relative;
    cursor: pointer;
    &:not(:first-child){
      margin-top: 8px;
    }
    &.false{
      .polls__answer-to-question{
        display: block;
        color: #E83F3F!important;
      }
      label{
        &:before{
          border-color: #E83F3F;
        }
        &:after{
          border-color: #E83F3F;
        }
      }
    }
    &.true{
      .polls__answer-to-question{
        display: block;
        color: #8BC540!important;
      }
      label{
        &:before{
          border-color: #8BC540;
        }
        &:after{
          border-color: #8BC540;
        }
      }

    }
    label{
      cursor: pointer;
      position: relative;
      display: block;
      width: 100%;
    }
    &.polls__list-radio{
      &.false{
        label{
          &:after{
            background-color: #E83F3F;
          }
        }
      }
      &.true{
        label{
          &:after{
            background-color: #8BC540;
          }
        }
      }
    }

  }
  &__list-radio{
    label{
      position: relative;
      padding-left: 38px;
      font-size: 18px;
      line-height: 24px;
      @media screen and (max-width: 620px){
        font-size: 14px;
        line-height: 19px;
        padding-left: 27px;
      }
      &:before{
        left: 0;
        top: 1px;
        width: 18px;
        height: 18px;
        border-radius: 50%;
        border: 1px solid #CACCDF;
        @include respondTo(small) {
          top: 0;
        }
      }
      &:after{
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background-color: #caccdf;
        left: 4px;
        top: 5px;
        @include respondTo(small) {
          top: 4px;
        }
      }
    }
  }
  &__list-checkbox{
    label{
      position: relative;
      padding-left: 38px;
      font-size: 18px;
      line-height: 24px;
      @media screen and (max-width: 620px){
        font-size: 14px;
        line-height: 19px;
        padding-left: 27px;
      }
      &:before{
        left: 0;
        top: 1px;
        width: 16px;
        height: 16px;
        border: 1px solid #CACCDF;
      }
      &:after{
        height: 6px;
        width: 10px;
        border-left: 2px solid #caccdf;
        border-bottom: 2px solid #caccdf;
        transform: rotate(-57deg);
        left: 3px;
        top: 5px;
      }
    }
  }
  &__wrapper-btn{
    margin-top: 25px;
    display: flex;
    @include respondTo(small) {
      flex-direction: column;
    }
  }
  &__btn{
    button {
      cursor: pointer;
      border: 2px solid transparent;
      border-radius: 40px;
      padding: 10px 20px;
      font-size: 18px;
      line-height: 28px;
      font-family: $ff-medium;
      @media screen and (max-width: 620px){
        padding: 7px 12px;
        font-size: 14px;
        line-height: 19px;
      }
      @include respondTo(small) {
        width: 100%;
      }
    }
  }
  &__btn-chek {
    &.unactive {
      pointer-events: none;
      button{
        background-color: transparent;
        border-color: #CACCDF;
      }
    }
    &.active{
      pointer-events: auto;
      button{
        color: #FFFFFF;
        background-color: #8BC540;
        border-color: #8BC540;
        &:hover{
          border-color: #a3e74c;
          background-color: #a3e74c;
        }
      }
    }



  }
  &__btn-repeat{
    display: none;
    position: relative;
    z-index: 2001;
    margin-left: 15px;
    @include respondTo(small) {
      margin-left: 0;
      margin-top: 15px;
    }
    &.active{
      display: block;
    }
    button{
      color: #FFFFFF!important;
      background-color: #8BC540;
      border-color: #8BC540;
      &:hover{
        border-color: #a3e74c;
        background-color: #a3e74c;
      }
    }
  }

  &__answer-to-question {
    display: none;
    font-size: 14px;
    line-height: 18px;
    font-family: $ff-medium;
    margin-top: -3px;
    @media screen and (max-width: 620px){
      margin-top: 0;
      font-size: 13px;
      line-height: 15px;
    }

  }
  &__window-stub{
    display: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: transparent;
    z-index: 2000;
    &.active{
      display: block;
    }
  }
  &__textarea{
    position: relative;
    textarea {
      box-sizing: border-box;
      resize: none;
      width: 100%;
      min-height: 148px;
      background-color: #fff;
      border: none;
      border-radius: 8px;
      padding: 16px 24px;
      font-size: 18px;
      line-height: 24px;
      color: #1C1B28;
      font-family: $ff-medium;
      outline: none;
      &:-moz-placeholder { color: rgba(28, 27, 40, 0.73); }
      &::-webkit-input-placeholder { color: rgba(28, 27, 40, 0.73); }
    }
  }

