.form-ocenka{
  &__row{
    display: flex;
    margin-left: -.625rem;
    margin-right: -.625rem;

    @include respondTo(tablet){
      flex-wrap: wrap;
      margin-bottom: -1.25rem; //20px
    }

    @include respondTo(mobile){
      margin-bottom: -1rem;
    }
  }

  &__col{
    flex: 0 0 25.4%;
    max-width: 25.4%;
    padding: 0 .625rem;

    @include respondTo(tablet){
      margin-bottom: 1.25rem; //20px
    }

    @include respondTo(mobile){
      margin-bottom: 1rem;
      flex-basis: 100%;
    }

    &:first-of-type{
      max-width: unset;
      flex-grow: 1;

      @include respondTo(tablet){
        flex-basis: 61.4%;
        max-width: unset;
      }
    }

    &:last-of-type{
      flex-basis: 16.11%;
      max-width: 16.11%;

      @media screen and (max-width: 1200px){
        flex-basis: 20%;
        max-width: 20%;
      }
    }

    &:nth-child(2n){
      @include respondTo(tablet){
        flex-basis: 37.5%;
        max-width: unset;
      }

      @include respondTo(mobile){
        flex-basis: 100%;
      }
    }

    &:nth-child(2n + 1){
      @include respondTo(tablet){
        flex-grow: 1;
        max-width: unset;
      }

      @include respondTo(mobile){
        flex-basis: 100%;
      }
    }
  }

  &__error{
    color: #ff0000;
    font-size: 1rem;
    margin: 2rem 0 0;
  }

  & .btn,
  & .select__choosen,
  & input{
    height: 3.75rem; //60px

    @include respondTo(tablet){
      height: 3rem; //48px
      padding: 0 1.25rem;
    }

    @include respondTo(mobile){
      height: 2.5rem;
      padding: 0 1rem;
    }
  }

  & .btn{
    @include respondTo(mobile) {
      height: 2.25rem; //36px
    }
  }

  & input{

    &::placeholder{
      color: $color-text-gray;
    }

    &::-moz-placeholder {
      line-height: 3.75rem;//60px

      @include respondTo(tablet) {
          line-height: 2.75rem;//44px
      }

      @include respondTo(mobile) {
          line-height: 2.25rem;//36px
      }
    }
  }
}
