.housing-cooperative-header {
  padding-bottom: 4rem;

  @include respondTo(tablet) {
    padding-bottom: 3rem;
    padding-left: 4rem;
    padding-right: 3rem;
  }

  @include respondTo(mobile) {
    padding-left: 1.25rem;
    padding-right: 1.25rem;
    padding-bottom: 2.5rem;
  }

  .page-title-block {
    margin-top: 3rem;

    @include respondTo(tablet) {
      margin-top: 4rem;
    }

    @include respondTo(mobile) {
      margin-top: 2rem;
    }
  }
  .page-desc {
    margin-top: 2.5rem;

    @include respondTo(tablet) {
      margin-top: 2rem;
      width: calc(100% - 4rem);
    }

    @include respondTo(mobile) {
      margin-top: 1.5rem;
    }
  }
}

.enter-to-housing-cooperative {
  @include respondTo(tablet) {
    padding-top: 3rem;
  }
  @include respondTo(mobile) {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  &__title {
    font-family: $ff-semi-bold;
    font-size: 3rem; //48px
    line-height: 1;
    letter-spacing: -0.015em;
    color: $color-text;
    padding-bottom: 2rem; //32px

    @include respondTo(tablet) {
      font-size: 2rem;
      padding-bottom: 1rem;
    }

    @include respondTo(mobile) {
      font-size: 1.5rem;
      padding-bottom: 1rem;
    }

    &_mobile-26 {
      @include respondTo(mobile) {
        font-size: 1.625rem;
        padding-bottom: 1.5rem;
      }
    }
  }

  &__footnote {
    margin-top: 1.875rem; //30px
    margin-bottom: 1.5rem; //24px
    z-index: 1;

    @include respondTo(tablet) {
      padding-right: 5rem;
    }

    @include respondTo(mobile) {
      margin-left: -1rem;
      margin-right: -1rem;
      padding: 1.5rem 1rem;
      border-radius: 1.25rem;
    }
  }

  &__footnote-des p {
    font-family: $ff-medium;
    font-size: 1.5rem; //24px
    line-height: 1.166;

    @include respondTo(tablet) {
      font-size: 1rem;
      line-height: 1.5;
    }

    @include respondTo(mobile) {
      font-size: .875rem; //14px
      line-height: 1.28;
    }
  }

  &__grid {
    @include respondTo(tablet) {
      row-gap: 2rem;
    }

    @include respondTo(mobile) {
      row-gap: 2.5rem;
    }
  }

  &__text {

    p {
      color: $color-text;
      font-size: 1.25rem; //20px
      line-height: 1.4;
      font-family: $ff-medium;

      &:not(:last-child) {
        margin-bottom: .75rem; //12px
      }

      @include respondTo(tablet) {
        font-size: 1rem;
        line-height: 1.5;
      }

      @include respondTo(mobile) {
        font-size: .875rem;
        line-height: 1.28;
      }
    }

  }
}

.numbers-of-housing-cooperative {
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 1.25rem; //20px

    @include respondTo(tablet) {
      margin-top: 1rem;
    }

    @include respondTo(mobile) {
      display: flex;
      flex-direction: column;
      row-gap: .75rem;
      margin-top: .5rem;
    }
  }
}

.condition-housing-cooperative {

  .block {

    @include respondTo(tablet) {
      padding-top: 3rem;
      padding-bottom: 3rem;
    }
    @include respondTo(mobile) {
      padding-top: 2rem;
      padding-bottom: 2rem;
    }
  }

  &__grid{
    display: grid;
    grid-template-columns: minmax(620px,832px) minmax(200px,322px);
    grid-row-gap: 48px;
    grid-column-gap: 20px;
    justify-content: space-between;
    @media screen and (max-width: 990px){
      grid-template-columns:1fr;
    }
    @media screen and (max-width: 720px){
      grid-row-gap: 0;
    }

    @include respondTo(tablet) {
      margin-top: .5rem;
      row-gap: 3rem;
    }

    @include respondTo(mobile) {
      margin-top: 0;
      row-gap: 2.5rem;

      .link-to-all {
        display: inline-block;
      }
    }
  }

  &__subtitle {
    font-family: $ff-semi-bold;
    font-size: 1.5rem;
    line-height: 1.083;
    letter-spacing: 0;
    color: $color-text;
    padding-bottom: 2rem; //32px

    @include respondTo(tablet) {
      font-size: 1.25rem;
      line-height: 1.2;
      padding-bottom: 1.5rem;
    }

    @include respondTo(mobile) {
      font-size: 1rem;
      line-height: 1.25rem;
      padding-bottom: 1.5rem;
      letter-spacing: -0.015em;
    }
  }

  &__list {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    row-gap: 1rem; //16px
    margin-bottom: 2rem; //32px

    @include respondTo(tablet) {
      row-gap: .75rem;
    }
  }

  &__list-item {
    font-size: 1.25rem; //20px
    line-height: 1.4;
    font-family: $ff-medium;
    color: $color-text;
    letter-spacing: 0;
    padding-left: 1.25rem; //20px
    position: relative;

    @include respondTo(tablet) {
      font-size: 1rem;
      line-height: 1.5;
    }

    @include respondTo(mobile) {
      line-height: 1.25;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: .5rem;
      width: .5rem;
      height: .5rem;
      background: $color-primary;
      border-radius: 50%;
    }
  }
}

.docs-housing-cooperative {
  .card-service {
    padding: 2rem 1.5rem 2.25rem 2rem;
    min-height: auto;

    @include respondTo(tablet) {
      padding: 1.5rem;
    }

    @include respondTo(mobile) {
      padding: 1.25rem;
    }
  }

  .card-service__content {
    display: flex;
    flex-direction: column;
    //row-gap: 1rem;
    height: auto;
    max-width: none;
    position: relative;
  }

  .card-service__title {
    font-family: $ff-semi-bold;
    font-size: 1.375rem;
    line-height: 1.18;
    max-width: none;
    letter-spacing: 0;

    @include respondTo(tablet) {
      margin-bottom: .5rem;
      font-size: 1.25rem;
      line-height: 1.2;
      letter-spacing: -0.01em;
    }

    @include respondTo(mobile) {
      margin-bottom: 0;
      line-height: 1.1;
      letter-spacing: 0;
    }
  }

  .card-service__list {
    display: grid;
    grid-template-columns: 2rem 1fr;
    grid-gap: .75rem;
    margin-top: 15px;

    @include respondTo(tablet) {
      align-items: center;
      grid-gap: 1rem;
    }

    @include respondTo(mobile) {
      align-items: start;
    }
  }

  .card-service__doc-title {
    font-family: $ff-base;
    font-size: .875rem;
    line-height: 1.28;
    color: $color-text-gray;

    @include respondTo(tablet) {
      font-family: $ff-medium;
      font-size: 1rem;
      line-height: 1.5rem;
    }

    @include respondTo(mobile) {
      font-size: .875rem;
      line-height: 1.28;
      align-items: start;
    }
  }
}

.housing-cooperative-faq {
  margin-top: .5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 1.25rem; //20px

  @include respondTo(tablet) {
    display: block;
  }

  @include respondTo(mobile) {
    margin-top: 1rem;
  }

  &__col {
    @include respondTo(tablet) {
      &:not(:first-child) {
        margin-top: -1px;
      }
    }
  }

  &__item {
    font-family: $ff-semi-bold;
  }

  &__body {
    font-family: $ff-medium;
    font-size: 1rem;
    line-height: 1.25;

    a {
      color: $color-text-accent;
      font-family: $ff-semi-bold;
    }

    @include respondTo(tablet) {
      padding-left: 0;
    }

    @include respondTo(mobile) {
      font-size: .75rem;
      line-height: 1.16;
    }
  }
}

.form-housing-cooperative {
  $form: #{&};

  @include respondTo(tablet) {
    min-height: auto;
    padding: 1.5rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 1.25rem;

  }

  @include respondTo(mobile) {
    display: block;
    padding: 1rem;
  }

  &__form {
    @include respondTo(tablet) {
      max-width: 15.75rem;
      margin-left: auto;
    }

    @include respondTo(mobile) {
      max-width: none;
      margin-left: 0;
    }
  }

  .btn[type=submit] {
    @include respondTo(tablet) {
      width: 100%;
    }
  }

  &__icons {
    display: flex;
    margin-top: 1.5rem;
    margin-bottom: 1.25rem;
    justify-content: space-between;
    column-gap: 1rem;

    @include respondTo(tablet) {
      column-gap: .75rem;
    }

    @include respondTo(mobile) {
      margin-top: 1rem;
      flex-direction: column;
      //row-gap: .75rem;
      margin-bottom: 1.5rem;
    }
  }

  &__icon {
    display: flex;
    align-items: center;
    column-gap: .5rem;
    cursor: pointer;
    &:not(:first-child){
      @include respondTo(mobile) {
        margin-top: 0.75rem;
      }

    }
  }

  &__btn {
    @extend %btn-reset;
    cursor: pointer;
    height: 1.5rem;

    & + span {
      font-family: $ff-medium;
      font-size: .875rem;
      line-height: 1.28;
    }
  }

  &__title {
    font-size: 1.25rem;
    line-height: 1.3;
    font-family: $ff-semi-bold;
    color: $color-text;
    margin-bottom: .5rem;

    @include respondTo(mobile) {
      letter-spacing: -0.015em;
    }
  }

  &__subtitle {
    font-size: .875rem;
    line-height: 1.28;
    font-family: $ff-medium;
    color: $color-text-gray;

    @include respondTo(mobile) {
      font-size: .75rem;
      line-height: 1.333;
    }
  }

  .feedback__checkbox {
    @include respondTo(mobile) {
      padding-bottom: 0;
    }
  }
}

.housing-cooperative-banner-wrapper {
  padding: 4.81rem .5rem 0;

  @include respondTo(tablet) {
    padding: 6rem 2rem 0;
  }

  @include respondTo(mobile) {
    padding: 4rem 1.25rem 0;
  }
}

.housing-cooperative-banner {
  background: $white;
  border-radius: 1.25rem;
  position: relative;
  padding: 44px 3rem 2.5rem;
  overflow: hidden;

  @include respondTo(tablet) {
    padding: 2.5rem;
  }

  @include respondTo(mobile) {
    padding: 1.5rem 1.25rem;

    /*&::before {
      content: '';
      position: absolute;
      top: 1.5rem;
      left: 1.25rem;
      width: calc(100% - 2.5rem);
      height: calc(100% - 3rem);
      background: rgba(red, 0.1);
    }*/
  }

  &__title {
    font-family: $ff-semi-bold;
    color: $color-text-main;
    font-size: 2.5rem;
    line-height: 1.1;
    letter-spacing: -0.015em;

    @include respondTo(tablet) {
      font-size: 1.625rem;
      line-height: 1;
      letter-spacing: -0.01em;
    }

    @include respondTo(mobile) {
      font-size: 1.5rem;
      line-height: 1;
      width: calc(100% - 3rem);
    }
  }

  &__btn {
    font-family: $ff-medium;
    column-gap: .75rem;
    margin-top: 3rem;

    @include respondTo(tablet) {
      margin-top: 2.5rem;
    }

    @include respondTo(mobile) {
      width: 100%;
      font-size: 1rem;
      line-height: 1.25;
      margin-top: 1.5rem;
    }
  }


  &__circle {
    width: 23.375rem;
    height: 23.375rem;
    right: 4.25rem;
    bottom: -7.8125rem;
    background: linear-gradient(18.05deg, rgba(254, 209, 65, 0.7) 12.34%, rgba(254, 199, 23, 0.119) 68.56%, rgba(254, 199, 23, 0) 85.05%);
    transform: rotate(-90deg);
    border-radius: 50%;
    position: absolute;

    @include respondTo(tablet) {
      width: 15.25rem;
      height: 15.25rem;
      right: 2.5rem;
      bottom: -4.625rem;
    }

    @include respondTo(mobile) {
      display: none;
    }
  }

  &__img {
    @include respondTo(mobile) {

    }
  }

  &__picture {
    position: absolute;
    width: 230px;
    height: 208px;
    right: 169px;
    bottom: 2px;

    @include respondTo(tablet) {
      width: 10.1875rem;
      height: 9.3125rem;
      bottom: 0;
      right: 5.75rem;
    }

    @include respondTo(mobile) {
      width: 4.8125rem;
      height: 4.5625rem;
      right: 0.4375rem;
      top: 1.25rem;
    }
  }
}
