.progress-bar {
    position: relative;
    width: 100%;
    height: 3px;
    background-color: #CACCDF;
    overflow: hidden;

    &.is-scrolled {
        position: fixed;
        top: 0;
        z-index: 100;
    }

    &__line {
        width: 0;
        height: 100%;
        background-color: #8BC540;
        transition: width .3s;
    }
}
